import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import styles from './_now-radio-buttons.scss';
import {
	getFormFieldMessageIds,
	renderFormFieldMessages,
	renderFormFieldLabel
} from '@servicenow/now-input';
import {messagesSchema} from '@servicenow/now-input';
import focusBehavior from '@servicenow/behavior-focus';

import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_BOOTSTRAPPED} = actionTypes;

const handleBlur = (e, dispatch) => {
	dispatch(({properties}) => {
		const {required, value, name, invalid} = properties;
		return {
			type: 'NOW_RADIO_BUTTONS#INVALID_SET',
			payload: {name, value: true},
			shouldDispatch: Boolean(required && !value && !invalid)
		};
	});
};

const handleClick = (e, option, dispatch) => {
	if (option.disabled || option.readonly) {
		return;
	}
	dispatch(({properties}) => {
		const {disabled, readonly, name} = properties;
		return {
			type: 'NOW_RADIO_BUTTONS#VALUE_SET',
			payload: {name, value: option.id},
			shouldDispatch: !disabled && !readonly
		};
	});
	dispatch(({properties}) => {
		const {required, name, invalid} = properties;
		return {
			type: 'NOW_RADIO_BUTTONS#INVALID_SET',
			payload: {name, value: false},
			shouldDispatch: required && invalid
		};
	});
};

const view = (state, {dispatch}) => {
	const {componentId} = state;
	const {
		disabled,
		helperContent,
		hideRequiredIndicator,
		invalid,
		label,
		layout,
		messages,
		name = `radio-buttons-${componentId}`,
		optional,
		options,
		readonly,
		required
	} = state.properties;
	return (
		<Fragment>
			{renderFormFieldLabel(
				componentId,
				invalid,
				required,
				optional,
				label,
				helperContent,
				hideRequiredIndicator
			)}
			<div
				class={{'now-radio-buttons': true, ['-' + layout]: layout}}
				id={`form-field-${componentId}`}
				role="radiogroup"
				aria-describedby={getFormFieldMessageIds(componentId, messages)}
				aria-label={label}>
				{options.map((option) => {
					return (
						<div className="now-radio-button">
							<input
								id={`radio-${componentId}-${option.id}`}
								class={{
									'now-radio-button-input': true,
									'is-invalid': invalid,
									'is-readonly': readonly || option.readonly
								}}
								autofocus={option.autofocus}
								checked={option.checked}
								disabled={
									disabled || readonly || option.disabled || option.readonly
								}
								name={name}
								required={required}
								type="radio"
								value={option.id}
								on-click={(e) => handleClick(e, option, dispatch)}
								on-blur={(e) => handleBlur(e, dispatch)}
							/>
							<label
								class={{
									'now-radio-button-label': true,
									'is-disabled': disabled || option.disabled
								}}
								htmlFor={`radio-${componentId}-${option.id}`}>
								<span className="now-line-height-crop">{option.label}</span>
							</label>
						</div>
					);
				})}
			</div>
			{renderFormFieldMessages(componentId, messages)}
		</Fragment>
	);
};

/**
 * A selectable input that lets the user choose
 * a single option in a predefined set of mutually exclusive options.
 *
 * ```jsx
 * <now-radio-buttons
 *   label="Choose one:"
 *   name="choice"
 *   options={[{id: 'opt1', label: 'Option 1'}]}
 * />
 * ```
 *
 * @seismicElement now-radio-buttons
 * @summary An interactive control for selecting a single input from a group of mutually exclusive choices.
 * @uib.label Radio buttons
 * @uib.icon radio-buttons
 * @uib.description An interactive control for selecting a single input from a group of mutually exclusive choices.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties label, value, optional, required, hideRequiredIndicator, disabled, readonly, helperContent, invalid, layout, messages, options, name
 * @uib.innerComponents now-button-iconic, now-icon, now-popover, now-input
 */
createEnhancedElement('now-radio-buttons', {
	properties: {
		/**
		 * When true, the user cannot interact with the field
		 * or change its value and the field appears greyed out.
		 * @type {boolean}
		 * @uib.description Gray out and prevent interaction
		 */
		disabled: {default: false, schema: {type: 'boolean'}},
		/**
		 * If set, an iconic button with a popover appears next to the field
		 * label to display additional hint text to the user, such as
		 * password requirements or other instructions for completing the field.
		 * @type {(string|JSX)}
		 * @uib.label Helper text
		 * @uib.description Whether to include an iconic button with popover next to the field label for displaying additional hint text to the user. Use plain text to allow the text to be translatable
		 * @uib.fieldType string
		 * @uib.translatable true
		 */
		helperContent: {},
		/**
		 * When true, the required indicator is visually hidden. Use this for
		 * forms where most fields are required, along with the `optional` flag
		 * for any fields that are not required.
		 * @type {boolean}
		 * @uib.label Hide Required Indicator
		 * @uib.description When true, the required indicator is visually hidden. Use this for forms where most fields are required, along with the `optional` flag for any fields that are not required.
		 */
		hideRequiredIndicator: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, indicates that the field value is invalid,
		 * which is visually reflected in the field display.
		 * @type {boolean}
		 * @uib.description Set this flag to indicate that the field is in an incorrect state, which will be visually reflected in the field display.
		 */
		invalid: {default: false, manageable: true, schema: {type: 'boolean'}},
		/**
		 * User-visible label for the field.
		 * @type {string}
		 * @uib.description Label describing the overall purpose of the radio group field
		 * @uib.defaultValue Choose one:
		 * @uib.translatable true
		 */
		label: {schema: {type: 'string'}},
		/**
		 * Determines whether the radio buttons are vertical or horizontal.
		 * @type {('vertical'|'horizontal')}
		 * @uib.description Whether the radio buttons should be laid out vertically or horizontally
		 */
		layout: {
			default: 'vertical',
			schema: {type: 'string', enum: ['vertical', 'horizontal']}
		},
		/**
		 * Array of inline alert messages that display below the field.
		 * @type {Array.<{status: ('critical'|'warning'|'positive'|'info'|'suggestion'), content: (string|JSX), icon: string}>}
		 * @uib.label Alert messages
		 * @uib.description Array of inline alert messages that are displayed below the field
		 * @uib.defaultValue []
		 */
		messages: {
			default: [],
			schema: messagesSchema
		},
		/**
		 * When true, the field is optional and does not need to be completed
		 * before submitting the form. Results in a visual indicator.
		 * @type {boolean}
		 * @uib.description Visually indicates that the field is optional
		 * @uib.defaultValue false
		 */
		optional: {default: false, schema: {type: 'boolean'}},
		/**
		 * Array of configuration objects to set behavior
		 * for the radio group. Each object must contain a unique `id`.
		 * @type {Array.<{id: (string|number), label: string, checked: boolean, readonly: boolean, disabled: boolean}>}
		 * @uib.description Array of objects providing configuration for the options available within the radio group
		 * @uib.defaultValue [{"id": "foo", "label": "Foo", "checked": true}, {"id": "bar", "label": "Bar", "readonly": true}, {"id": "baz", "label": "Baz", "disabled": true}]
		 * @uib.typeMetadata.emptyStateBehavior EMPTY_ARRAY
		 */
		options: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						id: {type: ['string', 'number']},
						label: {type: 'string', translatable: true},
						checked: {type: 'boolean'},
						readonly: {type: 'boolean'},
						disabled: {type: 'boolean'}
					}
				}
			}
		},
		/**
		 * Name of the field control. Must be unique if there are multiple
		 * radio group components on a page.
		 * @type {string}
		 * @uib.label Identifier
		 * @uib.description Name passed along in dispatched actions
		 */
		name: {schema: {type: 'string'}},
		/**
		 * When true, the user cannot modify the value of the field.
		 * @type {boolean}
		 * @uib.label Read-only
		 * @uib.description By enabling this property, the user cannot modify the value of the field. The field is not disabled in this setting.
		 * @uib.defaultValue false
		 */
		readonly: {default: false, schema: {type: 'boolean'}},
		/**
		 * When true, the user must enter a value in the
		 * input field before they can submit the form.
		 * @type {boolean}
		 * @uib.description This property allows you to indicate that the user must enter a value in the field before proceeding.
		 * @uib.defaultValue false
		 */
		required: {default: false, schema: {type: 'boolean'}},
		/**
		 * The `id` of the option selected by the user, or
		 * the first option passed in with `checked` set to `true`.
		 * @type {(string|number)}
		 * @uib.label Selected option
		 * @uib.defaultValue foo
		 * @uib.description Contains the `id` value of the option selected by the user, or the first option passed in with a `checked` value.
		 */
		value: {manageable: true, schema: {type: ['string', 'number']}}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({properties, updateProperties}) => {
			const {options} = properties;
			const selectedOption = options.find((option) =>
				option.hasOwnProperty('checked')
			);
			if (selectedOption) {
				updateProperties({value: selectedOption.id});
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user interacts with a radio button in the group.
		 * Payload contains the name of the control and the `id` of the
		 * currently selected radio button.
		 * @type {{name: string, value: (string|number)}}
		 * @uib.description Dispatched when the user interacts with a radio button in the group
		 */
		'NOW_RADIO_BUTTONS#VALUE_SET': {
			schema: {
				type: 'object',
				properties: {
					name: {type: 'object'},
					value: {type: 'string'}
				}
			}
		}
	},
	behaviors: [
		{
			behavior: focusBehavior
		}
	],
	styles,
	view
});
